.illegal-info .ant-form-item {
  margin-bottom: 6px;
}
.illegal-info-modal .info {
  margin-bottom: 16px;
}
.illegal-info-modal .info .text {
  font-weight: 700;
}
.illegal-info-modal .img-or-video {
  height: 400px;
  background-color: #000;
}
.illegal-info-modal .img-or-video video {
  display: block;
  width: 100%;
  height: 100%;
}
.illegal-info-modal .img-or-pic {
  height: 400px;
  overflow: auto;
}
.illegal-info-modal .img-or-pic img {
  display: inline-block;
  width: 48%;
  margin: 5px;
  padding: 7px;
  background-color: lightgrey;
  border-radius: 5px;
}
.illegal-info-modal .ant-form-item {
  margin-bottom: 0;
}
.carNum {
  border-radius: 2px;
  border: 1px solid white;
  text-align: center;
  padding: 2px 9px;
  display: inline-block;
  font-weight: bold;
}
.carColor1 {
  color: white;
  background-color: #1e68bb;
  box-shadow: 0 0 1px 2px #1e68bb;
}
.carColor2 {
  color: black;
  background-color: #f9b426;
  box-shadow: 0 0 1px 2px #f9b426;
}
.carColor3 {
  color: white;
  background-color: black;
  box-shadow: 0 0 1px 2px black;
}
.carColor4 {
  color: black;
  background-color: #5cc939;
  box-shadow: 0 0 1px 2px #5cc939;
}
.carColor5 {
  color: black;
  background-color: #d8d4d4;
  box-shadow: 0 0 1px 2px #d8d4d4;
}
