.illegal-info {
  .ant-form-item {
    margin-bottom: 6px;
  }
}
.illegal-info-modal {
  .info {
    margin-bottom: 16px;
    .text {
      font-weight: 700;
    }
  }
  .img-or-video {
    height: 400px;
    background-color: #000;
    video {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .img-or-pic {
    //display: flex;
    //flex-wrap: wrap;
    height: 400px;
    overflow: auto;
    img {
      display: inline-block;
      width: 48%;
      //height: inherit;
      //flex: 0 0 48%;
      margin: 5px;
      padding: 7px;
      background-color: lightgrey;
      border-radius: 5px;
    }
  }
  .ant-form-item {
    margin-bottom: 0;
  }
}

.carNum{
  border-radius: 2px;
  border: 1px solid white;
  text-align: center;
  padding: 2px 9px;
  display: inline-block;
  font-weight: bold;
}
